<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Test Login Databoom</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form  ref="form" v-model="valid" lazy-validation>
                  <v-text-field :rules="nameRules" prepend-icon="person" name="login" label="Login" v-model="username" type="text" required></v-text-field>
                  <v-text-field :rules="nameRules" id="password" prepend-icon="lock" name="password" v-model="password" label="Password" type="password" require></v-text-field>
                </v-form>
                <v-alert
                        :value="error"
                        type="error"
                >
                  Username o password errati.
                </v-alert>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" v-on:click="doLogin" :loading="loading">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>


<script>
    import axios from 'axios'
    export default {
        data: () => ({
            password:"",
            username:"",
            loading: false,
            error:false,
            nameRules: [
                v => !!v || 'Campo Richiesto',
            ],
        }),
        methods:{
            doLogin(){
                this.error=false;
                this.loading=true;
                let obj = {
                    username: this.username,
                    password: this.password
                }
                if (this.$refs.form.validate())
                {
                    axios.post("https://apistage.databoom.com/v1/auth/signin", obj).then((res) => {
                        this.loading = false;
                        window.location.href = `https://stage.databoom.com/authentication/signin?jwt=${res.data.jwt}`;
                        console.log(res);
                    }).catch((err) => {
                        this.loading = false;
                        this.error = true
                        console.log(err);
                    })
                }
            }
        }
    }
</script>

